import React, { Component } from 'react';
import {Link} from 'react-router-dom'
import {message, Modal} from 'antd';

import detectEthereumProvider from '@metamask/detect-provider';

import Routes from "./routes";
import {Utils, NETWORK_ID, BEEFCHAIN_RELEASE_TIME, checkTime} from './utils/utils';
import {HashRouter} from "react-router-dom";
import {addNetwork} from './utils/chainsConfig';


import './App.scss';
import Header from "./components/Header/Header";
import Wallets from "./components/Wallet/Wallets";
import CountDown from "./components/Countdown/Countdown";
import {CloseOutlined} from "@ant-design/icons";

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			lang:"en",
			address: null,
      showSwapModal:!localStorage.getItem('showSwapModal1')
		}
	}

	async componentDidMount() {
		this.readWeb3Instance();

		if (typeof window.ethereum !== 'undefined') {
			window.ethereum.on('accountsChanged', (accounts)=>{
				this.readWeb3Instance();
			})
			window.ethereum.on('chainChanged', (accounts)=>{
				console.log("chainChanged");
				window.location.reload();
			})
		}
		else if (typeof window.web3 !== 'undefined') {
			console.log(window.web3);
		}

    message.config({
      maxCount: 2,
    })
	}

  readWeb3Instance = async () =>{
    const provider = await detectEthereumProvider();

    if (provider) {
      // window.ethereum.enable();
      Utils.setWeb3(provider);
      provider
      .request({ method: 'eth_requestAccounts' })
      .then((accounts)=>{
        this.checkChainId(provider);
        this.handleAccountsChanged(accounts)
      })
      .catch((err) => {
        console.error(err);
      });
      provider.on('accountsChanged', (accounts)=>{this.handleAccountsChanged(accounts)})
      provider.on('chainChanged', (accounts)=>{
        window.location.reload();
      })
    }
    else {
      console.error('wait for MetaMask');
      Utils.setWeb3(false);
    }
  }

  handleAccountsChanged = (accounts) =>{
    if (accounts.length === 0) {
      // MetaMask is locked or the user has not connected any accounts
      console.log('Please connect to MetaMask.');

    } else if (accounts[0] !== this.state.address) {
      this.setState({
        address:accounts[0]
      })
    }
  }

  checkChainId = (web3) =>{
    if(parseInt(web3.chainId, 16) !== NETWORK_ID){
      addNetwork(NETWORK_ID);
    }
  }

  disconnect = () => {
    const tempAddress = this.state.address;
    this.setState({
      address: '0x00000000000000000000000000000000deadbeef',
      tempAddress: tempAddress,
    });
  };

  reconnect = () => {
    this.setState({
      address: this.state.tempAddress,
    });
  };

  toggleShowSwapModal = () =>{
    localStorage.setItem('showSwapModal1', "false")
    this.setState({
      showSwapModal: !this.state.showSwapModal
    })
  }

	render() {
		return (
			<HashRouter>
  			<div className="App">
          <div className={"container"}>
            <Header address={this.state.address}/>
            <Modal
              visible={this.state.showSwapModal}
              onCancel={this.toggleShowSwapModal}
              footer={null}
              centered={true}
              wrapClassName={'customModal'}
            >
              <div className={"showSwapModal"}>
                <CloseOutlined className="close" onClick={() => {
                  this.toggleShowSwapModal();
                }}/>
                <div className={"content"}>
                  {
                    checkTime(BEEFCHAIN_RELEASE_TIME) ?
                      <>
                        <h1>
                          BeefChain is Live!!!
                        </h1>
                      </>
                      :
                      <>
                        <h1>
                          BeefChain Launch In
                        </h1>
                        <CountDown date={BEEFCHAIN_RELEASE_TIME}/>
                      </>
                  }

                  <a href={'https://roastbeefchain.io'} target="_blank" rel={"noopener noreferrer"}>
                    <div className={"button"} onClick={this.toggleShowSwapModal}>
                      Go to BeefChain
                    </div>
                  </a>
                </div>

              </div>
            </Modal>
            <Routes
              address={this.state.address} disconnect={this.disconnect} reconnect={this.reconnect} readWeb3Instance={this.readWeb3Instance}/>
          </div>
        </div>
			</HashRouter>
		);
	}
}

export default App;
